import React from 'react'
import {Container, Typography,Grid,Autocomplete,TextField, MenuItem, Button,Alert} from '@mui/material'
import parse from 'autosuggest-highlight/parse';
import match from 'autosuggest-highlight/match';
import { MuiTelInput } from 'mui-tel-input'

function Home() {
  const topSymbol= [
    {title: "AAPL", "index": 0},
    {title: "AMGN", "index": 1},
    {title: "AXP", "index": 2},
    {title: "BA", "index": 3},
    {title: "CAT", "index": 4},
    {title: "CRM", "index": 5},
    {title: "CSCO", "index": 6},
    {title: "CVX", "index": 7},
    {title: "DIS", "index": 8},
    {title: "DOW", "index": 9},
    {title: "GS", "index": 10},
    {title: "HD", "index": 11},
    {title: "HON", "index": 12},
    {title: "IBM", "index": 13},
    {title: "INTC", "index": 14},
    {title: "JNJ", "index": 15},
    {title: "JPM", "index": 16},
    {title: "KO", "index": 17},
    {title: "MCD", "index": 18},
    {title: "MMM", "index": 19},
    {title: "MRK", "index": 20},
    {title: "MSFT", "index": 21},
    {title: "NKE", "index": 22},
    {title: "PG", "index": 23},
    {title: "TRV", "index": 24},
    {title: "UNH", "index": 25},
    {title: "V", "index": 26},
    {title: "VZ", "index": 27},
    {title: "WBA", "index": 28},
    {title: "WMT", "index": 29}
] 
const [symbol, setSymbol] = React.useState('');
  const [frequency, setFrequency] = React.useState('Daily');
  const frequencyOptions=[
    'Daily','Hourly','Weekly'
  ]

  const notificationOptions=[

    'Select','Email','SMS'
  ]
  const [thresholdPrice, setThresholdPrice] = React.useState('');
  const [notification, setNotification] = React.useState('Select');
  const priceRef = React.useRef(null);
  const symbolRef = React.useRef(null);
  const buttonRef = React.useRef(null);
  const [payLoad, setPayLoad] = React.useState("");
  const validateRef = React.useRef(null);
  const [phoneNumber, setPhoneNumber] = React.useState("+977Number");
  const [successStatus, setSuccessStatus] = React.useState(false);
  function phoneHnadler(value){
    setPhoneNumber(value)
    setPayLoad({
      ...payLoad,
      phoneNumber: value
    })
  }
  return (
    <Container 
        maxWidth="100%"
        sx={{
            backgroundColor: 'white',
            height: '100vh',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',

        }}
        >
        <Typography variant='h6'>STOCK PRICE ALERTER </Typography>
        {
          successStatus && <Alert severity="success" sx={{
            mt:2,
            mb:2
          }}>Alert created successfully</Alert>
        }
        <Alert severity="info">This is an Stock Price Alert When the threshold price of stock exceed</Alert>
        <Grid container spacing={2}
        sx={{
          mt:5
        }}
        >
                  <Grid item xs={12} sm={4} sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    
                  }}>
                    <Grid item xs={12} sm={12} sx={{
                      mt:4,
                      display: 'flex',
                      flexDirection: 'column',
                      alignItems: 'left',
                      justifyContent: 'left',
                      mb:2
                    }}>
                   <Typography variant='h6' sx={{
                      mb:2

                   }}>How to use?</Typography>                   

{/*                    
                    <Typography color="blue">The notification alert is sent to you when price of stock exceed threshol</Typography> */}
                    <Typography color="blue">Follow the steps below to create an alert.</Typography>
                    <br/>
                    <Typography variant="body2" gutterBottom>1. Select the share symbol from the dropdown.</Typography>
                    <Typography variant="body2" gutterBottom>2. Select the frequency of the alert.</Typography>
                    <Typography variant="body2" gutterBottom>3. Enter the threshold price.</Typography>
                    <Typography variant="body2" gutterBottom>4. Select the notification type.</Typography>
                    <Typography variant="body2" gutterBottom>5. Enter the email address or phone number.</Typography>
                    <Typography variant="body2" gutterBottom>6. Click on the submit button.</Typography>
                    </Grid>

                    
                    

                  
                  </Grid>
                  <Grid item xs={12} sm={8}
                  sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    width: '100%',
                    height: '100%',
                  }}
                  >
                  <Autocomplete
                    id="highlights-demo"
                    sx={{ width: 300 }}
                    options={topSymbol}
                    clearOnEscape={false}
                    onChange={(event, newValue) => {
                      setSymbol(newValue.title)

                    }
                  } 
                    
                    getOptionLabel={(option) => option.title}
                    renderInput={(params) => (
                      <TextField {...params} label="Share Symbol" margin="normal" helperText="Please select the share symbol" />
                    )}
                    renderOption={(props, option, { inputValue }) => {
                      const matches = match(option.title, inputValue, { insideWords: true });
                      const parts = parse(option.title, matches);
                      return (
                        <li {...props}>
                          <div>
                            {parts.map((part, index) => (
                              <span
                                key={index}
                                style={{
                                  fontWeight: part.highlight ? 700 : 400,
                                }}
                              >
                                {part.text}
                              </span>
                            ))}
                          </div>
                        </li>
                      );
                    }}
                  />
                  {/* //two fropdown options for frequency 'Daily' and 'Hourly' */}
                  <Typography ref={symbolRef} color={"red"} sx={{width:290}}></Typography>
                  <TextField
                    sx={{ width: 300,mb:2,mt:2}}
                    id="outlined-select-currency"
                    select
                    label="Frequency"
                    value={frequency}
                    helperText="Please select the frequency"
                    variant="outlined"
                  >
                    {frequencyOptions.map((option) => (
                      <MenuItem key={option} value={option} onClick={
                        (e)=>{
                          setFrequency(option)
                        }
                      }>
                        {option}
                      </MenuItem>
                    ))}

                  </TextField>
                  <TextField 
                    sx={{ width: 300,mb:2,mt:2}}
                    id="outlined-basic"
                    label="Threshold Price"
                    helperText="Please enter the threshold price in USD"
                    onChange={(e)=>{
                      console.log(e.target.value)
                      if(e.target.value>0 && e.target.value<1000000 && e.target.value.match(/^[0-9]+$/)){
                        setThresholdPrice(e.target.value)
                        setThresholdPrice(e.target.value)
                        priceRef.current.innerHTML = ""
                      }
                      else{
                        priceRef.current.innerHTML = "Price should be between 0 and 1000000 and should be a number"

                      }}}>                                         
                    </TextField>
                    <Typography ref = {priceRef} color={"red"} sx={{width:290}}></Typography>
                    <TextField 
                    sx={{ width: 300,mb:2,mt:2}}
                    id="notfication-type"
                    select
                    label="Notification Preference"
                    value={notification}
                    helperText="Please select the notification preference"
                    variant="outlined"
                  >
                    {notificationOptions.map((option) => (
                      <MenuItem key={option} value={option} onClick={
                        (e)=>{
                          setNotification(option)
                        }
                      }>
                        {option}
                      </MenuItem>
                    ))}
                  </TextField>
                  {notification === 'Email' && <TextField
                    sx={{ width: 300,mb:2,mt:2}}
                    id="outlined-basic"
                    label="Email"
                    helperText="Please enter your email address"
                    onChange={(e)=>{
                      setPayLoad(e.target.value)
                    }}>
                    </TextField>}
                    {/* {notification === 'SMS' && <TextField
                    sx={{ width: 300,mb:2,mt:2}}
                    accept="number"
                    id="outlined-basic"
                    label="Phone Number"
                    helperText="Please enter your phone number"
                    onChange={(e)=>{
                      setPayLoad(e.target.value)
                    }}>
                    </TextField>} */
                    notification === 'SMS' &&
                    <MuiTelInput value={phoneNumber} onChange={phoneHnadler} sx={{ width: 300,mb:2,mt:2}}
                    label="Phone Number"
                    helperText="Please enter your phone number"
                    auto />
                    
                    
                    }
                    <Typography ref={validateRef} color={"red"} sx={{width:290}}></Typography>
                  <Button ref={buttonRef} variant='contained' color='primary' sx={{ width: 300,mb:2,mt:2}} onClick={(e)=>{
                    
                    if(frequency && thresholdPrice && notification!=="Select" && symbol && notification && payLoad){
                      symbolRef.current.innerHTML = ""
                      validateRef.current.innerHTML = ""
                      buttonRef.current.innerHTML = "Sending..."
                      fetch('https://stock-notifier.onrender.com/api/v1/stockAlert',{
                        method: 'POST',
                        headers: {
                          'Content-Type': 'application/json'
                        },
                        body: JSON.stringify({
                          symbol: symbol,
                          frequency: frequency,
                          thresholdPrice: thresholdPrice,
                          notification: notification,
                          payLoad: payLoad
                        })
                      })
                      .then((res)=>res.json())
                      .then((data)=>{
                        console.log(data)
                        if(data.message){
                          buttonRef.current.innerHTML = "Notify Me"
                          validateRef.current.innerHTML = ""
                          setSuccessStatus(true)
                          setTimeout(() => {
                            setSuccessStatus(false)                            
                          }, 3000);
                          setFrequency("Select")
                          setThresholdPrice("")
                          setNotification("Select")
                          setSymbol("")
                          setPayLoad("")
                          
                        }
                        else{
                          buttonRef.current.innerHTML = "Notify Me"
                          validateRef.current.innerHTML = "Something went wrong"
                          setSuccessStatus(false)
                        }
                      }
                      )
                      .catch((err)=>{
                        console.log(err)
                      }
                      )
                    }
                    else{
                      if(symbol===''){
                        symbolRef.current.innerHTML = "Please select a symbol"

                      }
                      else if(notification==="Select"){
                        validateRef.current.innerHTML = "Please select a notification preference"

                      }
                      else{
                        validateRef.current.innerHTML = "Enter the Notitification details(Email/Phone Number)"
                      }
                    }
              

                  }}>Notify Me</Button>
                  {/* <Button variant='contained' color='primary' sx={{ width: 300,mb:2,mt:2}}>Cancel</Button> */}
                    
                  </Grid>
                  
            </Grid>

    </Container>
  )
}

export default Home